.MuiContainer-root.product-view {
  height: 80vh;
  display: flex;
  align-items: center;
  margin: 150px 0 100px 0;
  background-image: linear-gradient(to bottom right, #cc5973, #e0cfd3);
}

.product-view .image-wrapper img {
  width: 80%;
}

.product-view .text {
  color: black;
  margin-top: 5%;
}

.product-view .text h2 {
  font-size: 30px;
}

.product-view .text span h1 {
  font-size: 20px;
  font-weight: 300;
}

.product-view .text h3 {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 300;
}

.product-view .text .quantity {
  margin-bottom: 5px;
}

.MuiContainer-root.product-view .custom-button {
  color: black;
  background-color: lightcoral;
}

.MuiContainer-root.product-view .custom-button:hover {
  color: white;
}
